.explore {
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__list {
    @include page-grid;

    row-gap: 48px;
  }

  &__content {
    grid-column: 1 / -1;
    width: 100%;

    @include on-tablet {
      grid-column: 2 / -2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
    &__photo {
      @include hover(transform, scale(1.1));
    }
  }
}

.section-title__tablet {
  display: none;

  @include on-tablet {
    display: block;
  }

  @include on-desktop {
    display: none;
  }
}

.section-title__desktop {
  @include on-tablet {
    display: none;
  }

  @include on-desktop {
    display: block;
  }
}
